@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
html{scroll-behavior:smooth}body{font-family:"Roboto",sans-serif !important;background:#00002e}p{margin:0}.row{margin:0;padding:0}.p-0{padding:0px}div,ul,a,p,h1,h2,h3,h4,h5,h6,span,button{transition:background-color 1s ease-out}a{text-decoration:none !important}.btn-yellow{background:#feae00;border-radius:10px;font-style:normal;font-weight:500;font-size:18px;line-height:135%;color:#f4f9ff;border:none;outline:none}.btn-transparent{background:#00002e;border:1.5px solid #feae00;border-radius:10px;font-style:normal;font-weight:500;font-size:18px;line-height:135%;color:#feae00}
.banner{display:flex;justify-content:center;align-items:center;flex-direction:column;padding-top:49px}.banner p{font-style:normal;font-weight:400;font-size:20px;line-height:135%;text-align:center;color:#5e5e85;margin-bottom:40px}.banner h2{font-style:normal;font-weight:700;font-size:22px;line-height:135%;text-align:center;color:#f4f9ff;margin-bottom:72px}.banner .btn-yellow{padding:22px 70px}
.main-navbar{background:#00002e;border-bottom:1px solid #131348;padding:21px 0}.main-navbar .navbar-expand-lg .navbar-collapse{justify-content:flex-end !important}.main-navbar button{padding:12px 25px}.main-navbar i{color:#fff;font-size:22px}@media(max-width: 991px){.main-navbar .btn-yellow{margin:0 auto !important;margin-top:30px !important;display:block}}@media(max-width: 380px){.main-navbar .logo{width:150px}}
.choose{position:relative;display:flex;justify-content:center;align-items:center;flex-direction:column;padding-top:49px;padding-bottom:181px}.choose p{font-style:normal;font-weight:400;font-size:20px;line-height:135%;text-align:center;color:#5e5e85;margin-bottom:40px}.choose h2{font-style:normal;font-weight:700;font-size:22px;line-height:135%;text-align:center;color:#f4f9ff;margin-bottom:72px}.choose .btn-yellow{padding:22px 70px}.choose .choose-cards{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.choose .choose-cards .items{background:#080835;border-radius:10px;width:358px;height:390px;border:1.5px solid rgba(0,0,0,0);display:flex;justify-content:flex-start;align-items:center;flex-direction:column;margin-right:50px}.choose .choose-cards .items:last-child{margin:0 !important}.choose .choose-cards .items:hover{border:1.5px solid #feae00}.choose .choose-cards .items h2{margin-top:29px;margin-bottom:118px !important}.choose .wrapper{position:absolute;top:0;left:0;width:100%;height:100vh;display:flex;justify-content:center;align-items:center;flex-direction:column;background:rgba(1,0,33,.85);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px)}.choose .wrapper p{font-style:normal;font-weight:400;font-size:18px;line-height:135%;text-align:center;color:#f4f9ff;margin-top:27px}@media(max-width: 768px){.choose .choose-cards{flex-direction:column}.choose .choose-cards .items{margin:0;margin-bottom:30px}}@media(max-width: 380px){.choose .choose-cards .items{width:330px}}@media(max-width: 360px){.choose .choose-cards .items{width:300px}}
.progress-sec{position:relative}.progress-sec .bg-img{position:absolute;top:0;right:0}.progress-sec .logo{padding-top:60px;margin-bottom:50px}.progress-sec p{font-style:normal;font-weight:400;font-size:18px;line-height:135%;color:#feae00;margin-bottom:8px}.progress-sec button{padding:15px;margin-top:15px}.modal-content{background:#212529;border:1px solid #212529;border-radius:10px;padding:40px !important}.modal-content h6{font-style:normal;font-weight:700;font-size:22px;line-height:135%;text-align:center;text-transform:uppercase;color:#feae00;padding-bottom:20px}.modal-content p{font-style:normal;font-weight:400;font-size:16px;line-height:135%;text-align:center;color:#5e5e85;padding-bottom:30px}.modal-content button{width:100%;display:flex;justify-content:center;align-items:center}.modal-body{padding:0 !important}.table-responsive .table{border:2px solid #feae00 !important}.table-responsive .table thead th{border:unset !important}.table-responsive th{border:2px solid #feae00;color:#fff}.table-responsive td{border:2px solid #feae00;color:#fff}.bhbd{width:260px}@media(max-width: 600px){.progress-sec button{width:100%}}
