.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 49px;
  p {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 135%;
    text-align: center;
    color: #5e5e85;
    margin-bottom: 40px;
  }
  h2 {
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 135%;
    text-align: center;
    color: #f4f9ff;
    margin-bottom: 72px;
  }
  .btn-yellow {
    padding: 22px 70px;
  }
}
